import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Beyond } from '../../../utilities/beyond';
import { Global } from '../../../global/global';
import { NgxSpinnerService } from 'ngx-spinner';
import {  ViewChild, ChangeDetectorRef, NgZone } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { HttpClient, JsonpInterceptor } from "@angular/common/http";
@Component({
  providers:[Beyond],
  selector: 'app-proz-profile-confirmation',
  templateUrl: './proz-profile-confirmation.component.html',
  styleUrls: ['./proz-profile-confirmation.component.css']
})
export class ProzProfileConfirmationComponent implements OnInit {
  prozCode: any;
  prozDetailsData: any;
  firstName: any;
  lastName: any;
  typeOfBusiness: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public HttP: HttpClient,
    public toastr: ToastrService,
    private ngZone: NgZone,
    private spinner: NgxSpinnerService,
    private ref: ChangeDetectorRef,
  ) {
  }

  ngOnInit() {
    this.spinner.show()
    this.beyond.beyondAdmin()
    this.prozCode=this.activatedRoute.snapshot.params['prozId']
    console.log("prozCode"+this.prozCode)
    this.prozDetails()
    this.spinner.hide()
  }

  signInBtn(){
    localStorage.setItem('proServiceAreas', null);
    this.router.navigate(['/proz-dashboard/prozDashboard',this.prozCode])    
  }
  home(){
    localStorage.setItem('proServiceAreas', null);
    this.router.navigate(['/'])    
  }
  //PROZ DETAILS
  prozDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzDetails/' + this.prozCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozDetailsData = response.response;
  console.log(this.prozDetailsData)
          this.typeOfBusiness = this.prozDetailsData[0].typeOfBusiness
        
         
          return false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //PROZ DETAILS

}
