  //TRADE LICENSE GENERATION
  createTradeLicense(prozProfileData) {
    return this.formBuilder.group({
      stateRegisteredIn: [prozProfileData.stateRegisteredIn, Validators.required],
      tradeLicense: [prozProfileData.tradeLicense, Validators.required],
      tradeLicenseNumber: [prozProfileData.tradeLicenseNumber, Validators.required],
      tradeLicenseExpiration: [prozProfileData.tradeLicenseExpiration, Validators.required]
    });
  }

  addFieldValue() {
    this.items = this.addTradeLicenseList.get('tradeLicense') as FormArray;
    this.items.push(this.createTradeLicense(''));
    this.checkValidationTrade()
  }

  get tradeLicenseValues(): FormArray {
    return this.addTradeLicenseList.get('tradeLicense') as FormArray;
  }

  deleteFieldValue(index: number) {
    this.tradeLicenseValues.removeAt(index);
    this.checkValidationTrade()
  }
  //VIEW PROZ DETAILS
  viewProzDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProDetails/' + this.prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          //this.spinner.hide()
          this.estimatedServiceCost = 0;
          this.totalHouseHolds = 0;
          this.prozProfileData = response.profile[0];

          if (this.prozProfileData.image == 'assets/img/proz/company_default.jpg') {
            this.prozProfileData.image = this.prozProfileData.image
          } else {
            this.prozProfileData.image = Global.url + this.prozProfileData.image
          }
          if (this.prozProfileData.proProfileImage == 'assets/img/default person.jpg') {
            this.prozProfileData.proProfileImage = this.prozProfileData.proProfileImage
          } else {
            this.prozProfileData.proProfileImage = Global.url + this.prozProfileData.proProfileImage
          }
          this.prozServiceData = response.serviceAreas;
          this.prozPaymentData = response.payment[0];
          //console.log("prozPaymentData"+JSON.stringify(this.prozPaymentData))
          this.prozProfileData.tradeLicenseList = []

          this.subCategoryProz(this.prozProfileData.tradeLicense)
          this.prozProfileData.stateRegisteredIn.split(',').forEach((e, i) => {
            let tradeDetailsObj = {
              stateRegisteredIn: this.prozProfileData.stateRegisteredIn.split(',')[i],
              tradeLicense: this.prozProfileData.tradeLicense.split(',')[i],
              tradeLicenseNumber: this.prozProfileData.tradeLicenseNumber.split(',')[i],
              tradeLicenseExpiration: this.prozProfileData.tradeLicenseExpiration.split(',')[i],
            }


            this.prozProfileData.tradeLicenseList.push(tradeDetailsObj)
            //console.log(this.prozProfileData.tradeLicenseList)
            if (i === this.prozProfileData.stateRegisteredIn.split(',').length - 1) {
              this.prozProfileData.tradeLicenseList.forEach((e) => {
                this.categoriesData.forEach((f, j) => {
                  if (e.tradeLicense == f.cat_code) {
                    e.name = f.categoryName
                  }
                })
                this.items = this.addTradeLicenseList.get('tradeLicense') as FormArray;
                this.items.push(this.createTradeLicense(e));
              })
            }
          })
          this.statusValue = true

          for (var i = 0; i < response.serviceAreas.length; i++) {
            this.estimatedServiceCost = parseInt(this.estimatedServiceCost) + parseInt(response.serviceAreas[i].serviceCost)
            this.totalHouseHolds = parseInt(this.totalHouseHolds) + parseInt(response.serviceAreas[i].households)
            this.zipcodes.push(response.serviceAreas[i].zipcode)
          }
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
