  //EDIT PROFILE
  //SUB CATEGORIES PROZ
  subCategoryProz(categoryCode) {
    //console.log('cat')
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    var catCodeObj = {
      catCodes: categoryCode
    }
    this.http.post(Global.url + '/subCategoryFromCategoryCounty', catCodeObj, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          var list = response.response;
          console.log(response.response)
          var subcatArray = this.prozProfileData.areaOfExpertise.split(',')
          console.log(subcatArray)
          list.forEach((e, i) => {
            subcatArray.forEach((f, j) => {
              if (e.subcat_code == f) {
                this.areaExpertiseList.push(e.subcategoryName)
              }
            })
            if (i == list.length - 1) {
              console.log(this.areaExpertiseList)
              this.areaStatus = true
            }
          })
        } else {

        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //SUB CATEGORIES PROZ
  //CATEGORIES PROZ
  categoryProz(prozId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProCategories/' + prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.subCategoriesList = response.response;
          for (var i = 0; i < this.subCategoriesList.length; i++) {
            this.serviceCostTotal = this.serviceCostTotal + parseInt(this.subCategoriesList[i].serviceCost)
          }
          return false;
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //CATEGORIES PROZ  
  //FETCH PROZ LOCATION
  prozLocation(prozId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzLocation/' + prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozLocationFetch = response.response;
          this.getCountiesList(this.activatedRoute.snapshot.params['prozId'])
          google.maps.event.addDomListener(window, 'load', this.initializeZipMap(this.prozLocationFetch));
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
