  // Loads Geojson data on map function

  onMapReady(map: Map, key) {

    this.spinner.show()
    this.mapReg = map
    // this.showMap=true
    this.HttP.get("assets/geolocations.json").subscribe((json: any) => {
      console.log(json)
      json.features.map(val => {
        val.checked = false
      })
      this.json = json;
      var myStyle = { // Define your style object
        "color": "blue",
        "fillColor": ' rgb(242,239,233)',
        "weight": 0.5,
        "fillOpacity": 0.05,
        "opacity": 1,

      };
      // this.countyStore.forEach((a)=>{

      L.geoJSON(this.json, {

        style: myStyle,

        onEachFeature: (feature, layer) => {

          // popup content
          //TASK#468-fixed
          var popupContent = '<p>' + feature.properties.NAME + '</p>';
          // pop content ends here
          //Open data in Popup on Map
          layer.bindPopup(popupContent);
          // Open data in Popoup on map ends
          // Click event on Map
          //console.log(this.countyStore.length)
          let name = feature.properties.NAME.toUpperCase()
          this.countyStore.forEach((a) => {
            if (a.county == name && a.stateCode == feature.properties.STATE) {
              //console.log(a)
              feature['checked'] = true
              var style = {
                fillColor: 'yellow',
                fillOpacity: 0.5,
                weight: 0.2,
                opacity: 0.8,
              }
              var highlight = {
                'fillColor': 'green',
                'weight': 0.2,
                'opacity': 1
              };
              layer['setStyle'](style);
              layer['setStyle'](highlight);
            }
          })
          layer.on('mouseover', function () {
            if (feature['checked'] == true) {

            } else {
              var style = {
                fillColor: 'orange',
                fillOpacity: 0.5,
                weight: 0.2,
                opacity: 0.8,
              }
              layer['setStyle'](style);
            }
            layer.openPopup()
          });
          layer.on('mouseout', function () {
            if (feature['checked'] == true) {
            } else {
              var style = {
                "color": "blue",
                "fillColor": ' rgb(242,239,233)',
                "weight": 0.5,
                "fillOpacity": 0.05,
                "opacity": 1,

              }

              layer['setStyle'](style);
            }

          });

          layer.on({
            'click': (e) => {
              let name = feature.properties.NAME.toUpperCase()
              this.featuresSelected = feature.properties.NAME;
              //console.log(feature)
              if (feature['checked'] == true) {
                e.target.closePopup();
                feature['checked'] = false
                var style = {
                  fillColor: ' rgb(242,239,233)',
                  fillOpacity: 0.05,
                  weight: 0.2,
                  opacity: 1,
                }
                layer.bindPopup['true'];

                var val = this.featuresSelected.toUpperCase();
                this.delteCountyOnMap(this.featuresSelected.toUpperCase())
                layer['setStyle'](style);
                layer['setStyle'](highlight);
              } else if (feature['checked'] == false) {
                //console.log(layer)
                //console.log(feature)
                feature['checked'] = true
                var style = {
                  fillColor: 'green',
                  fillOpacity: 0.5,
                  weight: 0.2,
                  opacity: 1,
                }
                var highlight = {
                  'fillColor': 'green',
                  'weight': 0.2,
                  'opacity': 1
                };
                this.featuresSelected = feature.properties.NAME;
                layer['setStyle'](style);
                layer['setStyle'](highlight);
                this.getCountyManualy(this.featureSelected, feature.properties.STATE)
                console.log(feature.properties.STATE)
                this.getZipcodesFromCountyCounty(this.featuresSelected, feature.properties.STATE)
              }
            }
          });
          // Click event on Map ends here
        },
      }).addTo(map);
      if (this.prozLocationFetch[0].longitude == 0 || this.prozLocationFetch[0].latitude == 0) {

      } else {
        L.marker([this.prozLocationFetch[0].latitude, this.prozLocationFetch[0].longitude]).addTo(map);
        map.setView([this.prozLocationFetch[0].latitude, this.prozLocationFetch[0].longitude], 7);
      }
      this.spinner.hide();
      // })
    });

  }
