  // MANUAL ENTERY OF COUNTY
  getCountyManualy(featuresSelected, stateCode) {
    this.spinner.show()
    this.ngZone.run(() => {
      var categoryName = [];
      for (var i = 0; i < this.prozLocationFetch[0].tradeLicense.split(',').length; i++) {
        var subCategoryListAlias = this.subCategoriesList
        var subCategoriesList = subCategoryListAlias.filter(value => value.cat_code === this.prozLocationFetch[0].tradeLicense.split(',')[i])
        var catObj = {
          serviceName: subCategoriesList[0].serviceCategoryName,
          serviceCode: subCategoriesList[0].cat_code,
          serviceCategories: [],
          checkedNewLead: false
        }
        subCategoriesList.forEach((e) => {
          catObj.serviceCategories.push({
            baseHousehold: e.baseHousehold,
            cat_code: e.cat_code,
            categoryName: e.categoryName,
            checked: e.checked,
            checkedNew: e.checkedNew,
            checkedNewLead: e.checkedNewLead,
            createdBy: e.createdBy,
            createdOn: e.createdOn,
            estimatedCost: e.estimatedCost,
            id: e.id,
            modifiedBy: e.modifiedBy,
            modifiedOn: e.modifiedOn,
            monthlyFee: e.monthlyFee,
            perLead: e.perLead,
            perLeadCost: e.perLeadCost,
            prozId: e.prozId,
            qDiscount: e.qDiscount,
            quaterlyFee: e.quaterlyFee,
            serviceCategoryName: e.serviceCategoryName,
            serviceSubCategoryName: e.serviceSubCategoryName,
            status: e.status,
            subcat_code: e.subcat_code,
            subcategoryDescription: e.subcategoryDescription,
            subcategoryName: e.subcategoryName,
            subscriptionType: e.subscriptionType,
            yDiscount: e.yDiscount,
            yearlyFee: e.yearlyFee,
          })
        })
        categoryName.push(catObj)
        if (i === this.prozLocationFetch[0].tradeLicense.split(',').length - 1) {
          // this.countyList.push({
          //   proCode: this.activatedRoute.snapshot.params['prozId'],
          //   checked: false,
          //   county: this.featuresSelected,
          //   households: 0,
          //   category: categoryName,
          //   subCategoriesListMonthly: categoryName,
          //   subCategoriesListPerLead: categoryName,
          //   totalEstimatedCost: 0
          // })
          var countyObj = {
            county: this.featuresSelected
          }
          this.getZipcodesFromCountyData(countyObj, stateCode, 'ts')
          //this.applyChange('manual')
          categoryName = []
        }
      }
      //localStorage.setItem('proServiceAreas', JSON.stringify(this.countyList));
      //this.totalNumberOfHouses = parseInt(this.totalNumberOfHouses) + parseInt(response.response[response.response.length - 1][2])
    })
  }
  // MANUAL ENTERY OF COUNTY
  countyNamesDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getCountyNames', options)
      .subscribe(data => {
        this.countyNamesData = data.json();
        this.zipcodeCountiesData = this.countyNamesData.response;
        for (var i = 0; i < this.zipcodeCountiesData.length; i++) {
          this.countyNames.push({
            "countyName": this.zipcodeCountiesData[i].county,
          })
          if (i == this.zipcodeCountiesData.length - 1) {
            this.dataServices = this.completerService.local(this.countyNames, 'countyName', 'countyName');
            this.dataServiceCategory = true;
          }
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

