  //PROZ DETAILS
  prozDetails(prozId) {
    this.spinner.show();
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzDetails/' + prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozInDetail.firstName = response.response[0].firstName;
          this.prozInDetail.lastName = response.response[0].lastName;
          this.prozInDetail.mobileNumber = response.response[0].mobileNumber;
          this.prozInDetail.emailId = response.response[0].emailId;
          this.prozInDetail.smsMobile = response.response[0].smsMobile;
          return false;
        }
      }, error => {
        this.toastr.warning("Unable to Connect to server")
      })
  }
  //PROZ DETAILS
  //FETCH PROZ LOCATION
  prozLocation(prozId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzLocation/' + prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozLocationFetch = response.response;
          var addressMixed = this.prozLocationFetch[0].businessAddressLine1 + "," + this.prozLocationFetch[0].city + "," + this.prozLocationFetch[0].zipCode
          this.geocoding.getLatLongViaZipcode(addressMixed)
            .subscribe(address => {
              var coordObj = {
                lat: address.lat,
                lng: address.lng,
              }
              // google.maps.event.addDomListener(window, 'load', this.initializeZipMap(coordObj));
            })

          this.categoryProz(response.response[0].tradeLicense)
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //FETCH PROZ LOCATION 
  //TEMPORARY TIM STORAGE OF COUNTIES
  temporaryStorage() {
    this.totalNumberOfHouses = 0
    if (JSON.parse(localStorage.getItem('proServiceAreas')) == null || JSON.parse(localStorage.getItem('proServiceAreas')) == '') {
      this.countyStatus = false
    } else {
      this.ngZone.run(() => {
        this.countyList = JSON.parse(localStorage.getItem('proServiceAreas'))
      })
      this.countyList.forEach((e) => {
        this.selected[e.county] = (!this.selected[e.county]) ? true : false;
        this.counties.push(e.county)
        this.totalNumberOfHouses = parseInt(this.totalNumberOfHouses) + parseInt(e.households)
        //LOAD LAYER
        this.layerSet.set("styles", [{
          where: "'name' IN('" + this.counties.join("','") + "')",
          polygonOptions: {
            fillColor: "#00FF00"
          }
        }]);
        //LOAD LAYER
      })
      this.countyStatus = true
    }
  }
  //TEMPORARY TIM STORAGE OF COUNTIES
  //CATEGORIES PROZ
  categoryProz(categoryCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    var catCodeObj = {
      catCodes: categoryCode
    }
    this.http.post(Global.url + '/subCategoryFromCategoryCounty', catCodeObj, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.subCategoriesList = response.response;
          console.log(this.subCategoriesList)
          this.subCategoriesList.forEach((e, i) => {
            e.prozId = this.activatedRoute.snapshot.params['prozId']
            e.categoryName = categoryCode;
            e.checked = false;
            e.checkedNew = false;
            e.checkedNewLead = false;
            e.allowCheck = true;
            e.estimatedCost = 0
            e.perLeadCost = 'NA'
            e.subscriptionType = 'PerLead'
            e.allowCatCheck = true
            if (i == this.subCategoriesList.length - 1) {
              this.getFee(categoryCode)
            }
          })
        } else {
          this.noSubCategory = true
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //CATEGORIES PROZ
  getFee(catCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    var catCodeObj = {
      catCodes: catCode
    }
    this.http.post(Global.url + '/subCategoryFromPerLeadFee', catCodeObj, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.Feedata = response.response;
          this.subCategoriesList.forEach((e, i) => {
            this.Feedata.forEach((f) => {
              console.log(f)
              console.log(e)
              if (e.cat_code == f.cat_code) {
                if (e.subcat_code == f.subcat_code) {
                  e.allowCatCheck = false
                  e.allowCheck = false
                  e.perLeadCost = 0
                  e.perLead = f.perLead
                  e.baseHousehold = 0
                }
              }
            })
            if (i == this.subCategoriesList.length - 1) {
              console.log(this.subCategoriesList)
              this.getCountiesList(this.activatedRoute.snapshot.params['prozId'])
            }
          })
        } else {
          console.log('no data')
          this.noSubCategory = true
          this.subCategoriesList.forEach((e) => {
            e.perLeadCost = 'NA'
            e.allowCheck = true
            e.allowCatCheck = true
            e.baseHousehold = 0
          })
          console.log(this.subCategoriesList)
          this.getCountiesList(this.activatedRoute.snapshot.params['prozId'])
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //VIEW PROZ PROFILE
  viewProzDetails(prozCode) {
    this.totalServiceCost = 0;
    this.totalNumberOfHouses = 0;
    this.alternateHouseHolds = 0;
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzServiceArea/' + prozCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          var countyList = response.response
          countyList.forEach((e) => {
            e.checked = true
            this.selected[e.county] = (!this.selected[e.county]) ? true : false;
            this.counties.push(e.county)
            e.households = e.totalHouseHolds
            this.totalNumberOfHouses = this.totalNumberOfHouses + parseInt(e.totalHouseHolds);
            e.totalEstimatedCost = 0
            //LOAD LAYER
            this.layerSet.set("styles", [{
              where: "'name' IN('" + this.counties.join("','") + "')",
              polygonOptions: {
                fillColor: "#00FF00"
              }
            }]);
            //LOAD LAYER
            this.countyList.push({
              checked: e.checked,
              households: e.households,
              county: e.county,
            })
          })
          this.countyStatus = true
          this.prozArrayExists = true;
          return false;
        } else {
          this.prozArrayExists = false;
        }
      }, error => {
        this.toastr.warning("Unable to Connect to server")
      })
  }
