  //RUIN COUNTY LAYER
  ruinCountyLayer(county) {
    this.totalNumberOfHouses = 0
    this.ngZone.run(() => {
      this.counties = this.counties.filter(value => value !== county);
      this.countyList = this.countyList.filter(value => value.county !== county);
      localStorage.setItem('proServiceAreas', JSON.stringify(this.countyList));
      this.countyList.forEach((e) => {
        e.firstName = this.prozInDetail.firstName
        e.lastName = this.prozInDetail.lastName
        e.mobileNumber = this.prozInDetail.mobileNumber
        e.emailId = this.prozInDetail.emailId
        e.prozId = this.activatedRoute.snapshot.params['prozId']
        e.totalHouseHolds = e.households
        this.totalNumberOfHouses = parseInt(this.totalNumberOfHouses) + parseInt(e.households)
      })
      this.countyList.length == 0 ? this.countyStatus = false : this.countyStatus = true;
      this.layerSet.set("styles", [{
        where: "'name' IN('" + this.counties.join("','") + "')",
        polygonOptions: {
          fillColor: "#00FF00"
        }
      }]);
      this.infoWindowSet.open(null);
      this.deleteCountyAndPro({ county: county })
    })
  }
