  //DELETE ZIPCODES

  //POPULATE COUNTY
  populateCounty() {
    var subCategoriesListCount = this.subCategoriesList.filter(value => value.checked == true);
    if (this.countyList.length == 0) {
      // this.toastr.info('Select Counties Before Submission')
      return false;
    }
    if (subCategoriesListCount.length == 0) {
      this.toastr.info('Select Categories Before Submission')
      return false;
    }
    this.countyList = this.countyList.filter(value => value.checked == true);
    this.countyList.forEach((element) => {
      element.firstName = this.prozInDetail.firstName
      element.lastName = this.prozInDetail.lastName
      element.mobileNumber = this.prozInDetail.mobileNumber
      element.emailId = this.prozInDetail.emailId
      element.prozId = this.activatedRoute.snapshot.params['prozId']
      element.totalHouseHolds = element.households
    })
    var countyObj = {
      prozId: this.activatedRoute.snapshot.params['prozId'],
      countyList: this.countyList
    }
    this.http.post(Global.url + '/populateCounties', countyObj)
      .subscribe(data => {
        var response = data.json();
        if (response.status === 200) {
          this.populateCategories()
        } else {
          this.toastr.warning('Unable to connect to server');
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //POPULATE COUNTY
  //POPULATE CATEGORIES
  populateCategories() {
    this.subCategoriesList = this.subCategoriesList.filter(value => value.checked == true);
    var subCategoryObj = {
      prozId: this.activatedRoute.snapshot.params['prozId'],
      subCategoriesList: this.subCategoriesList
    }
    this.http.post(Global.url + '/populateCategories', subCategoryObj)
      .subscribe(data => {
        var response = data.json();
        if (response.status === 200) {
          //localStorage.setItem('proServiceAreas', null);
          this.router.navigate(['/prozProfile/prozSubscriptionConfirmation', this.activatedRoute.snapshot.params['prozId']])
          return false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //POPULATE CATEGORIES
