  //GET COUNTIES LIST
  getCountiesList(proCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.countyList = []
    this.ngZone.run(() => {
      this.http.get(Global.url + '/getUniqueCounty/' + proCode, options)
        .subscribe(data => {
          var response = data.json();
          var categoryName = []
          if (response.status == 200) {
            // this.spinner.hide()
            this.countyStatus = true
            this.countyStore = response.response;
            console.log(this.countyStore)
            this.showMap = true
            this.countyStore.forEach((e) => {
              this.selected[e.county] = (!this.selected[e.county]) ? true : false;
              this.counties.push(e.county)
            })

            this.countyStore.forEach((f, j) => {
              let data = this.prozLocationFetch[0].tradeLicense.split(',')
              data.forEach((g, k) => {
                var subCategoryListAlias = this.subCategoriesList
                var subCategoriesList = subCategoryListAlias.filter(value => value.cat_code === g)
                if (subCategoriesList.length > 0) {
                  console.log(subCategoriesList)
                  if (subCategoriesList.length == 1 && subCategoriesList[0].perLead == 'NA') {
                    var catObj = {
                      serviceName: subCategoriesList[0].serviceCategoryName,
                      allowCatCheck: subCategoriesList[0].allowCatCheck,
                      serviceCode: subCategoriesList[0].cat_code,
                      serviceCategories: [],
                      checkedNewLead: false
                    }
                  }
                  else {
                    var catObj = {
                      serviceName: subCategoriesList[0].serviceCategoryName,
                      allowCatCheck: subCategoriesList[0].allowCatCheck,
                      serviceCode: subCategoriesList[0].cat_code,
                      serviceCategories: [],
                      checkedNewLead: false
                    }
                  }

                }

                console.log(catObj)

                subCategoriesList.forEach((e) => {
                  catObj.serviceCategories.push({
                    baseHousehold: e.baseHousehold,
                    cat_code: e.cat_code,
                    categoryName: e.categoryName,
                    checked: e.checked,
                    checkedNew: e.checkedNew,
                    checkedNewLead: e.checkedNewLead,
                    createdBy: e.createdBy,
                    createdOn: e.createdOn,
                    estimatedCost: e.estimatedCost,
                    id: e.id,
                    modifiedBy: e.modifiedBy,
                    modifiedOn: e.modifiedOn,
                    monthlyFee: e.monthlyFee,
                    perLead: e.perLead,
                    allowCheck: e.allowCheck,
                    perLeadCost: e.perLeadCost,
                    prozId: e.prozId,
                    qDiscount: e.qDiscount,
                    quaterlyFee: e.quaterlyFee,
                    serviceCategoryName: e.serviceCategoryName,
                    serviceSubCategoryName: e.serviceSubCategoryName,
                    status: e.status,
                    subcat_code: e.subcat_code,
                    subcategoryDescription: e.subcategoryDescription,
                    subcategoryName: e.subcategoryName,
                    subscriptionType: e.subscriptionType,
                    yDiscount: e.yDiscount,
                    yearlyFee: e.yearlyFee,
                  })
                })


                categoryName.push(catObj)

                if (k == data.length - 1) {
                  this.countyList.push({
                    proCode: this.activatedRoute.snapshot.params['prozId'],
                    county: f.county,
                    state: f.state,
                    stateCode: f.stateCode,
                    households: 0,
                    subCategoriesListPerLead: categoryName,
                  })
                  this.ref.detectChanges()
                  ////console.log(this.countyList)
                  categoryName = []
                  this.getCountiesCategoryList(proCode)
                }
                //
              })

              if (j == this.countyStore.length - 1) {

                //  this.spinner.hide()
              }
            })


            //LOAD LAYER

            // this.loadSelected(this.countyStore)
            //LOAD LAYER

          } else {
            this.countyStatus = false
            // this.spinner.hide()
          }
        }, error => {
          this.toastr.warning("Unable to connect to server")
        })
    })



  }
  //GET COUNTIES LIST
  //LOAD MAP COLOR ON LOAD
  loadSelected(countyStore) {
    this.HttP.get("assets/geolocations.json").subscribe((json: any) => {
      json.features.map(val => {
        val.checked = false
      })
      this.json = json;


      L.geoJSON(this.json, {
        onEachFeature: (feature, layer) => {
          // popup content
          var popupContent = '<h4>NAME: ' + feature.properties.NAME + '</h4><b>COUNTY: ' + feature.properties.COUNTY + '</b>';
          // pop content ends here
          //Open data in Popup on Map
          layer.bindPopup(popupContent);
          // Open data in Popoup on map ends
          // Click event on Map
          // layer.on({
          // 'click': (e) => {
          countyStore.forEach((e) => {
            let name = feature.properties.NAME.toUpperCase()
            if (e.county === name) {
              feature['checked'] = true
              var style = {
                fillColor: 'yellow',
                fillOpacity: 0.5,
                weight: 2,
                opacity: 1,
                dashArray: '3'
              }
              var highlight = {
                'fillColor': 'green',
                'weight': 2,
                'opacity': 1
              };
              this.featuresSelected = feature.properties.NAME;
              layer['setStyle'](style);
              layer['setStyle'](highlight);
              // this.getCountyManualy(this.featureSelected)
            }
          })
          // }
          // });
          // Click event on Map ends here
        },
      })
    });
  }
