  //GET COUNTIES CATEGORY LIST

  //GET COUNTIES SUB CATEGORY LIST
  getCountiesSubCategoryList(proCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getCountiesSubCategoryList/' + proCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.countySubCategory = response.response;
          for (var i = 0; i < this.countyList.length; i++) {
            for (var k = 0; k < this.countyList[i].subCategoriesListPerLead.length; k++) {
              for (var j = 0; j < this.countyCategory.length; j++) {
                if (this.countyList[i].proCode === this.countyCategory[j].proCode && this.countyList[i].county === this.countyCategory[j].county && this.countyList[i].subCategoriesListPerLead[k].serviceCode === this.countyCategory[j].categoryCode) {
                  this.countyList[i].subCategoriesListPerLead[k].checkedNewLead = true
                }
              }
            }
          }

          for (var i = 0; i < this.countyList.length; i++) {
            for (var k = 0; k < this.countyList[i].subCategoriesListPerLead.length; k++) {
              for (var l = 0; l < this.countyList[i].subCategoriesListPerLead[k].serviceCategories.length; l++) {
                for (var m = 0; m < this.countySubCategory.length; m++) {
                  if (this.countyList[i].proCode === this.countySubCategory[m].proCode && this.countyList[i].county === this.countySubCategory[m].county && this.countyList[i].subCategoriesListPerLead[k].serviceCode === this.countySubCategory[m].categoryCode && this.countyList[i].subCategoriesListPerLead[k].serviceCategories[l].subcat_code === this.countySubCategory[m].subcategoryCode) {
                    this.countyList[i].subCategoriesListPerLead[k].serviceCategories[l].checkedNewLead = true
                    this.countyList[i].subCategoriesListPerLead[k].serviceCategories[l].perLeadCost = this.countySubCategory[m].perLead
                  }
                }
              }
            }
          }

        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
