  createTradeLicense(prozProfileData) {
    ////console.log(prozProfileData)
    return this.formBuilder.group({
      stateRegisteredIn: [prozProfileData.stateRegisteredIn, Validators.required],
      tradeLicense: [prozProfileData.tradeLicense, Validators.required],
      tradeLicenseNumber: [prozProfileData.tradeLicenseNumber, Validators.required],
      tradeLicenseExpiration: [prozProfileData.tradeLicenseExpiration, Validators.required]
    });
  }

  addFieldValue() {
    this.items = this.addTradeLicenseList.get('tradeLicense') as FormArray;
    ////console.log(this.items)
    this.items.push(this.createTradeLicense(''));
    this.checkValidationTrade()
  }

  get tradeLicenseValues(): FormArray {
    return this.addTradeLicenseList.get('tradeLicense') as FormArray;
  }

  deleteFieldValue(index: number) {
    this.tradeLicenseDeleteObj.index = index
    this.tradeLicenseDeleteObj.categoryCode = this.addTradeLicenseList.get('tradeLicense').value[index].tradeLicense
    this.tradeLicenseValues.removeAt(index);
    this.checkValidationTrade()
  }
  //DELETE LICENSES
  deleteLicenses() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.post(Global.url + '/deleteLicenses', this.tradeLicenseDeleteObj, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.toastr.success("Trade License Updated Successfully")
          window.location.reload()
          return false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //DELETE LICENSES
  //CHECK TRADE VALIDATIONS
  checkValidationTrade() {
    this.tradeStatusCheck = true
    this.addTradeLicenseList.get('tradeLicense').value.forEach((e) => {
      if (e.stateRegisteredIn == "" || e.tradeLicense == "" || e.tradeLicenseExpiration == "" || e.tradeLicenseNumber == "") {
        this.tradeStatusCheck = true
        return false;
      } if (e.stateRegisteredIn == null || e.tradeLicense == null || e.tradeLicenseExpiration == null || e.tradeLicenseNumber == null) {
        this.tradeStatusCheck = true
        return false;
      } else if (e.stateRegisteredIn !== "" && e.tradeLicense !== "" && e.tradeLicenseExpiration !== "" && e.tradeLicenseNumber !== "") {
        this.tradeStatusCheck = false
        return false;
      }
    })
  }
  //CHECK TRADE VALIDATIONS
  //ADD TRADE LICENSE
  addTradeLicense() {
    var state = []
    var tradeLicense = []
    var tradeLicenseNumber = []
    var tradeLicenseExpiration = []
    var tradeLicenseCategoryNames = []
    this.addTradeLicenseList.get('tradeLicense').value.forEach((e) => {
      state.push(e.stateRegisteredIn)
      tradeLicense.push(e.tradeLicense)
      tradeLicenseNumber.push(e.tradeLicenseNumber)
      tradeLicenseExpiration.push(e.tradeLicenseExpiration)
      tradeLicenseCategoryNames.push(e.tradeLicense)
    })

    var tradeDetailsObj = {
      "prozId": this.activatedRoute.snapshot.params['prozId'],
      "stateRegisteredIn": state,
      "tradeLicense": tradeLicense,
      "tradeLicenseNumber": tradeLicenseNumber,
      "tradeLicenseExpiration": tradeLicenseExpiration,
    }
    this.http.post(Global.url + '/bulkTradeLicenseDetailsInsert', tradeDetailsObj)
      .subscribe(data => {
        var response = data.json();
        if (response.status === 200) {
          if (this.tradeLicenseDeleteObj.categoryCode === '') {
            this.toastr.success("Trade License Updated Successfully")
            window.location.reload()
            return false;
          } else {
            this.deleteLicenses()
          }
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //ADD TRADE LICENSE
  //VIEW PROZ DETAILS
  viewPro(prozCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzProfile/' + prozCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozProfileData = response.response[0];
          this.prozProfileData.tradeDetails = []
          this.prozProfileData.stateRegisteredIn.split(',').forEach((e, i) => {
            let tradeDetailsObj = {
              stateRegisteredIn: this.prozProfileData.stateRegisteredIn.split(',')[i],
              tradeLicense: this.prozProfileData.tradeLicense.split(',')[i],
              tradeLicenseNumber: this.prozProfileData.tradeLicenseNumber.split(',')[i],
              tradeLicenseExpiration: this.prozProfileData.tradeLicenseExpiration.split(',')[i],
            }
            this.prozProfileData.tradeDetails.push(tradeDetailsObj)

            if (i === this.prozProfileData.stateRegisteredIn.split(',').length - 1) {
              this.prozProfileData.tradeDetails.forEach((e) => {
                this.items = this.addTradeLicenseList.get('tradeLicense') as FormArray;
                this.items.push(this.createTradeLicense(e));
              })
            }
          })
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //VIEW PROZ DETAILS
  //STATES
  getListOfStates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfStates', options)
      .subscribe(data => {
        var response = data.json();
        this.statesData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //STATES
  //CATEGORIES
  getListOfCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfCategories', options)
      .subscribe(data => {
        var response = data.json();
        this.categoriesData = response.response;
        this.categoriesData.forEach((e) => {
          e.checked = false;
        })
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
