  //DELETE TOTAL COUNTY ON CLICK ON MAP
  delteCountyOnMap(county) {
    this.ngZone.run(() => {
      this.countyList = this.countyList.filter(value => value.county !== county);
      localStorage.setItem('proServiceAreas', JSON.stringify(this.countyList));
      this.counties = this.counties.filter(value => value !== county.county);
      //console.log(county)
      this.countyStore = this.countyStore.filter(value => value.county !== county);
      //console.log(this.countyStore)
      this.deleteCountyAndPro(county)
      //  this.spinner.show()
      this.countyList.length == 0 ? this.countyStatus = false : this.countyStatus = true;
      // this.layerSet.set("styles", [{
      //   where: "'name' IN('" + this.counties.join("','") + "')",
      //   polygonOptions: {
      //     fillColor: "#00FF00"
      //   }
      // }]);
    })
  }
