  //GET ZIPCODES FROM COUNTY
  getZipcodesFromCountyCounty(data, stateCode) {
    var countyData = data
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getZipcodeFromCounty/' + data + '/' + stateCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          var displayZipcodes = response.response;
          displayZipcodes.forEach((e) => {
            e.proCode = this.activatedRoute.snapshot.params['prozId']
            e.checked = true
          })
          ////// console.log('call insert')
          this.insertCountiesZipcodeBatchDirect(countyData, displayZipcodes)
          // return false;
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
