  //DELETE PRO AND COUNTY
  //APPLY CHANGE
  applyChange(mode) {
    if (this.countyList.length == 0) {
      this.toastr.info('Select Counties Before Submission')
      return false;
    } else {
      this.countyList.forEach((data, index) => {
        var validateSubmit = data.subCategoriesListPerLead.filter(val => val.checkedNewLead == true)
        if (validateSubmit.length > 0) {
          this.disableButton = false
        } else {
          this.disableButton = true
        }
        if (index == this.countyList.length - 1) {
          if (this.disableButton == false) {
            this.spinner.show()
            let applyChangeObj = {
              "prozId": this.activatedRoute.snapshot.params['prozId'],
              "firstName": this.prozInDetail.firstName,
              "lastName": this.prozInDetail.lastName,
              "mobileNumber": this.prozInDetail.mobileNumber,
              "emailId": this.prozInDetail.emailId,
              "serviceArea": 'NA',
              "serviceRadius": '0',
              "serviceOffered": 'NA',
              "subServices": 'NA',
              "paymentType": 'NA',
              "noOfHouseholds": 'NA',
              "serviceCost": 'NA',
              "totalServiceCost": 0,
              "totalHouseHolds": 0,
              "houseHolds": 0,
              "zipcode": 'NA'
            }
            if (mode === "button") {
              this.spinner.show()
            }
            this.http.post(Global.url + '/addServiceArea', applyChangeObj)
              .subscribe(data => {
                var response = data.json();
                //////// console.log(response)
                if (response.status === 200) {
                  // this.insertCountiesBatch(mode)
                  this.spinner.hide()

                } else {
                  this.toastr.warning('Unable to connect to server');
                }
              }, error => {
                this.toastr.warning('Unable to connect to server');
              })
          } else {
            this.toastr.info('Please Select atleast one service to proceed')
          }
        }
      })
    }
  }
