  //GET ZIPCODES FROM COUNTY NON INSERT
  getZipcodesFromCountyData(data, stateCode, key) {
    if (key == 'view') {
      this.spinner.show();

      setTimeout(() => {
        /** spinner ends after 5 seconds */
        this.spinner.hide();
      }, 2000);
    }
    this.displayZipcodes = []
    var updateZipcodeHouses = false;
    this.showZipcode = false
    var householdsZipcodes = 0
    var county = data
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getZipcodeFromCounty/' + data.county + '/' + stateCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.zipcodeValidationEmpty = false
          this.displayZipcodes = response.response;
          this.displayZipcodes.forEach((e) => {
            e.proCode = this.activatedRoute.snapshot.params['prozId']
            e.zipcode = e.zipCode
            e.checked = false
            householdsZipcodes = householdsZipcodes + parseInt(e.households)
          })
          this.countyList.forEach((e) => {
            if (e.county === county.county) {
              e.households = householdsZipcodes
            }
          })
          //this.getZipcodesHouseholds(this.displayZipcodes)

          this.getZipcodesFromCounty(county, stateCode)
          return false;
        } else {
          this.displayZipcodes = []
          this.zipcodeValidationEmpty = true
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
