  //GET HOUSEHOLDS FOR ZIPCODES
  //GET ZIPCODES FROM COUNTY
  getZipcodesFromCounty(data, stateCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getZipcodeFromCountyTable/' + data.county + '/' + stateCode + '/' + this.activatedRoute.snapshot.params['prozId'], options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          var displayZipcodes = response.response;
          // console.log(displayZipcodes)
          for (var i = 0; i < displayZipcodes.length; i++) {
            for (var j = 0; j < this.displayZipcodes.length; j++) {
              if (displayZipcodes[i].zipcode === this.displayZipcodes[j].zipcode) {
                this.displayZipcodes[j].checked = true;
                // console.log('true')
              }
              if (i == displayZipcodes.length - 1) {
                this.showZipcode = true
              }
            }
          }
          return false;
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
