declare var jQuery:any;
declare var $:any;

export class multipleselectservice {
    public Multipleselectservice(){
    
  $(document).ready(function () {
    
    var next = 0;
    $("#add-more").click(function(e){
        e.preventDefault();
        var addto = "#field" + next;
        var addRemove = "#field" + (next);
        next = next + 1;
        var newIn = 
        ' <div id="field'+ next +'" name="field'+ next +'"> <div class="row"> <div class="col-sm-6"><div class="form-group"> <label for="serviceareas">Service Areas <span class="mandatory">*</span></label>  <span class="input-icon icon-right"> <input type="text" class="form-control" id="serviceareas" placeholder="Enter Zip Code"  #search formControlName="serviceArea">  <i class="fa fa-map-marker"></i>  </span>  </div>   </div> <div class="col-sm-6">  <div class="form-group">     <label for="serviceradius">Service Radius (in Miles) <span class="mandatory">*</span></label>   <select class="form-control" id="serviceradius" name="status" data-bv-field="row-title" formControlName="serviceRadius">   <option value="">--Select Service Radius--</option> <option value="10">10</option>  <option value="25">25</option>   <option value="50">50</option> <option value="100">100</option>  <option value="150">150</option> <option value="200">200</option>    </select> </div>  </div></div>';
        var newInput = $(newIn);
        var removeBtn = '<button id="remove' + (next) + '" class="btn btn-danger remove-me" >Remove</button></div></div><div id="field">';
        var removeButton = $(removeBtn);
        $(addto).after(newInput);
        $(addRemove).after(removeButton);
        $("#field" + next).attr('data-source',$(addto).attr('data-source'));
        $("#count").val(next);  
        
            $('.remove-me').click(function(e){
                e.preventDefault();
                var fieldNum = this.id.charAt(this.id.length-1);
                var fieldID = "#field" + fieldNum ;
                $(this).remove();
                $(fieldID).remove();
                
            });
    });

});
    
}
}