  //PROZ DETAILS
  prozDetails(prozId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzDetails/' + prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozDetailsData = response.response;
          console.log(this.prozDetailsData)
          var addressMixed = this.prozDetailsData[0].businessAddress + "," + this.prozDetailsData[0].city + "," + this.prozDetailsData[0].zipCode
          this.geocodingService.getLatLongViaZipcode(addressMixed)
            .subscribe(address => {
              if (address == undefined || address == '') {
                this.coordinates.latitude = 0
                this.coordinates.longitude = 0
              } else {
                var coordObj = {
                  lat: address.lat,
                  lng: address.lng,
                }
                this.coordinates.latitude = address.lat
                this.coordinates.longitude = address.lng
              }
              // google.maps.event.addDomListener(window, 'load', this.initializeZipMap(coordObj));
            })
          this.serProzDetails.get("companyName").setValue(this.prozDetailsData[0].typeOfBusiness)
          this.serProzDetails.get("firstName").setValue(this.prozDetailsData[0].firstName)
          this.serProzDetails.get("lastName").setValue(this.prozDetailsData[0].lastName)
          this.serProzDetails.get("businessPhone").setValue(this.prozDetailsData[0].mobileNumber)
          this.serProzDetails.get("smsMobile").setValue(this.prozDetailsData[0].smsMobile)
          this.serProzDetails.get("businessEmail").setValue(this.prozDetailsData[0].emailId)
          this.serProzDetails.get("businessType").setValue(this.prozDetailsData[0].businessType)
          this.serProzDetails.get("businessAddressLine1").setValue(this.prozDetailsData[0].businessAddress)
          this.serProzDetails.get("state").setValue(this.prozDetailsData[0].state)
          this.serProzDetails.get("city").setValue(this.prozDetailsData[0].city)
          this.serProzDetails.get("zipCode").setValue(this.prozDetailsData[0].zipcode)
          this.changeBusinessName()
          return false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
