  //YELP SELECTION
  selectYelp(yelp) {
    // this.coordinates.latitude = yelp.coordinates.latitude
    // this.coordinates.longitude = yelp.coordinates.longitude
    this.yelpId = yelp.alias
    this.http.get(Global.url + '/getYelpBusinessRating/' + yelp.alias)
      .subscribe(data => {
        var response = data.json()
        this.yelpRatingNo = response.rating
        if (this.yelpId != null && this.googleRatingId != null) {
          if (this.googleRating == "NA" || this.yelpRatingNo == "NA") {
            this.aggregateRating = "NA"
          } else {
            this.aggregateRating = (parseFloat(this.yelpRatingNo) + parseFloat(this.googleRating)) / 2
          }
          this.yelpValidate = false
        } else {
          this.yelpValidate = true
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //YELP SELECTION
  removeDuplicates(arr) {
    let unique_array = []
    for (let i = 0; i < arr.length; i++) {
      if (unique_array.indexOf(arr[i]) == -1) {
        unique_array.push(arr[i])
      }
    }
    return unique_array
  }

