import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Beyond } from '../../../utilities/beyond';
import { Global } from '../../../global/global';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms'
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  providers: [Beyond],
  selector: 'app-proz-subscription-confirmation',
  templateUrl: './proz-subscription-confirmation.component.html',
  styleUrls: ['./proz-subscription-confirmation.component.scss']
})
export class ProzSubscriptionConfirmationComponent implements OnInit {
  public zipcodes = [];
  public subCategoriesList = [];
  totalServiceCost: any;
  totalNumberOfHouses: any;
  alternateHouseHolds: any;
  prozServiceAreaData: any = [];
  totalProzServiceAreaData: any = [];
  prozArrayExists: boolean = false;
  totalMonthlyStatus: boolean = false;
  totalQuarterlyStatus: boolean = false;
  totalYearlyStatus: boolean = false;
  countyStatus: boolean = false;
  showConfirmation: boolean = false;
  totalYearly: any;
  totalMonthly: any;
  totalQuarterly: any;
  monthlyCat: any = '';
  quaterlyCat: any = '';
  yearlyCat: any = '';
  prozLocationFetch: any;
  layerSet: any;
  public selected = {};
  public countyList = []
  public serviceCostTotal: any = 0
  public counties = []
  public countyZipcode = []
  public countyCategory = []
  public countySubCategory = []
  public displayZipcodes = []
  perLeadStatus: boolean = false;
  monthlyStatus: boolean = false;
  totalCostSubscription: any = 0;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private spinner: NgxSpinnerService,
  ) { }

  ngOnInit() {
    this.spinner.show()
    // this.viewProzDetails(this.activatedRoute.snapshot.params['prozId'])
    // this.viewTotalProzServiceDetails(this.activatedRoute.snapshot.params['prozId'])
    // this.prozLocation(this.activatedRoute.snapshot.params['prozId'])
    // this.categoryProz(this.activatedRoute.snapshot.params['prozId'])
    this.getCountiesList(this.activatedRoute.snapshot.params['prozId'])
  }

  //VIEW TOTAL PROZ SERVICE
  viewTotalProzServiceDetails(prozCode) {
    this.totalMonthly = 0;
    this.totalQuarterly = 0;
    this.totalYearly = 0;
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzServiceArea/' + prozCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.totalProzServiceAreaData = response.response;
          for (var i = 0; i < this.totalProzServiceAreaData.length; i++) {
            if (this.totalProzServiceAreaData[i].paymentType == "Monthly") {
              this.totalMonthly = this.totalMonthly + parseInt(this.totalProzServiceAreaData[i].serviceCost);
              this.monthlyCat = this.monthlyCat + this.totalProzServiceAreaData[i].serviceOffered + ", "
              this.totalMonthlyStatus = true;
            } else if (this.totalProzServiceAreaData[i].paymentType == "Quarterly") {
              this.totalQuarterly = this.totalQuarterly + parseInt(this.totalProzServiceAreaData[i].serviceCost);
              this.quaterlyCat = this.quaterlyCat + this.totalProzServiceAreaData[i].serviceOffered + ", "
              this.totalQuarterlyStatus = true;
            } else if (this.totalProzServiceAreaData[i].paymentType == "Yearly") {
              this.totalYearly = this.totalYearly + parseInt(this.totalProzServiceAreaData[i].serviceCost);
              this.yearlyCat = this.yearlyCat + this.totalProzServiceAreaData[i].serviceOffered + ", "
              this.totalYearlyStatus = true;
            } else if (parseInt(this.totalProzServiceAreaData[i].serviceCost) == 0 && this.totalProzServiceAreaData[i].paymentType == "Monthly") {
              this.totalMonthlyStatus = false;
            } else if (parseInt(this.totalProzServiceAreaData[i].serviceCost) == 0 && this.totalProzServiceAreaData[i].paymentType == "Quarterly") {
              this.totalQuarterlyStatus = false;
            } else if (parseInt(this.totalProzServiceAreaData[i].serviceCost) == 0 && this.totalProzServiceAreaData[i].paymentType == "Yearly") {
              this.totalYearlyStatus = false;
            }
          }
          // this.prozArrayExists = true;
          return false;
        } else {
          // this.prozArrayExists = false;
        }
      }, error => {
        this.toastr.warning("Unable to Connect to server")
      })
  }
  //VIEW TOTAL PROZ SERVICE

  //VIEW PROZ SERVICE DETAILS
  viewProzDetails(prozCode) {
    this.totalServiceCost = 0;
    this.totalNumberOfHouses = 0;
    this.alternateHouseHolds = 0;
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzServiceArea/' + prozCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozServiceAreaData = response.response;
          this.countyList = response.response;
          for (var i = 0; i < this.countyList.length; i++) {
            this.countyList[i].households = this.countyList[i].totalHouseHolds
            this.totalNumberOfHouses = this.totalNumberOfHouses + parseInt(this.countyList[i].totalHouseHolds);
          }
          this.prozArrayExists = true;
          return false;
        } else {
          this.prozArrayExists = false;
        }
      }, error => {
        this.toastr.warning("Unable to Connect to server")
      })
  }
  //VIEW PROZ SERVICE DETAILS
  //DELETE SERVICE AREA
  deleteServiceArea(prozData) {
    this.zipcodes = []
    let prozDataObj = {
      prozId: this.activatedRoute.snapshot.params['prozId'],
      id: prozData.id
    }
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.post(Global.url + '/deleteProzProfileServiceAreas', prozDataObj, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.viewProzDetails(this.activatedRoute.snapshot.params['prozId'])
          this.prozLocation(this.activatedRoute.snapshot.params['prozId'])
          return false;
        } else {
          this.toastr.warning("Unable to delete specific record")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //DELETE SERVICE AREA
  //DELETE CHANGE
  deleteChange(item) {
    this.batchServiceAreaDelete(item)
  }
  //DELETE CHANGE  
  //BATCH SERVICE AREA DELETE
  batchServiceAreaDelete(item) {
    this.zipcodes = []
    let prozDataObj = {
      prozId: this.activatedRoute.snapshot.params['prozId'],
      zipcode: item.zipcode
    }
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.post(Global.url + '/batchServiceAreaDelete', prozDataObj, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozServiceAreaData = []
          this.viewProzDetails(this.activatedRoute.snapshot.params['prozId'])
          this.prozLocation(this.activatedRoute.snapshot.params['prozId'])
          this.viewTotalProzServiceDetails(this.activatedRoute.snapshot.params['prozId'])
          return false;
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //BATCH SERVICE AREA DELETE

  //FETCH PROZ LOCATION
  prozLocation(prozId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzLocation/' + prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozLocationFetch = response.response;
          return false;
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //FETCH PROZ LOCATION 

  //OPEN COLLAPSE
  openCollapse(item) {
    item.openCollapse = false;
    item.closeCollapse = true;
  }
  //OPEN COLLAPSE
  //CLOSE COLLAPSE
  closeCollapse(item) {
    item.openCollapse = true;
    item.closeCollapse = false;
  }
  //CLOSE COLLAPSE  

  payBtn() {
    this.router.navigate(['/prozProfile/prozProfileConfirmation', this.activatedRoute.snapshot.params['prozId']])
  }

  cancelBtn() {
    //this.router.navigate(['/proz/prozProfileServiceAreas', this.activatedRoute.snapshot.params['prozId']])
  }

  backBtn() {
    this.router.navigate(['/prozProfile/prozProfileServiceAreas', this.activatedRoute.snapshot.params['prozId']])
  }

  modalYes() {
    this.router.navigate(['/'])
  }

  //CATEGORIES PROZ
  categoryProz(prozId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProCategories/' + prozId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.subCategoriesList = response.response;
          for (var i = 0; i < this.subCategoriesList.length; i++) {
            this.serviceCostTotal = this.serviceCostTotal + parseInt(this.subCategoriesList[i].serviceCost)
          }
          return false;
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //CATEGORIES PROZ

  subcriptionYes() {
    this.router.navigate(['/prozProfile/prozSubscriptionConfirmation', this.activatedRoute.snapshot.params['prozId']])
  }


  //GET COUNTIES LIST
  getCountiesList(proCode) {

    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getUniqueCounty/' + proCode, options)
      .subscribe(data => {
        var response = data.json();
        //console.log(response.response)
        if (response.status == 200) {
          this.counties = response.response;
          this.getCountiesCategoryList(proCode)
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //GET COUNTIES LIST

  //GET COUNTIES ZIPCODE LIST
  getCountiesZipcodeList(proCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getCountiesZipcodeList/' + proCode, options)
      .subscribe(data => {
        var response = data.json();
        //console.log(response.response)
        if (response.status == 200) {
          this.countyZipcode = response.response;
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //GET COUNTIES ZIPCODE LIST

  //GET COUNTIES CATEGORY LIST
  getCountiesCategoryList(proCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getCountiesCategoryList/' + proCode, options)
      .subscribe(data => {
        var response = data.json();
        //console.log(response.response)
        if (response.status == 200) {
          this.countyCategory = response.response;
          this.getCountiesSubCategoryList(proCode)
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //GET COUNTIES CATEGORY LIST

  //GET COUNTIES SUB CATEGORY LIST
  getCountiesSubCategoryList(proCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getCountiesSubCategoryList/' + proCode, options)
      .subscribe(data => {
        var response = data.json();
        //console.log(this.counties)
        //console.log(this.countyCategory[0].stateCode)
        //console.log(response.response)
        if (response.status == 200) {
          this.countyStatus = true
          this.countySubCategory = response.response;
          for (var i = 0; i < this.counties.length; i++) {
            this.counties[i].categories = []
            this.counties[i].subcategories = []
            for (var j = 0; j < this.countyCategory.length; j++) {
              if (this.counties[i].county === this.countyCategory[j].county && this.counties[i].stateCode == this.countyCategory[j].stateCode) {
                this.counties[i].categories.push(this.countyCategory[j])
              }
            }
            for (var k = 0; k < this.countySubCategory.length; k++) {
              if (this.countySubCategory[k].subscriptionType === 'perLead') {
                //console.log(this.countySubCategory[k])
                this.totalCostSubscription = this.totalCostSubscription + parseInt(this.countySubCategory[k].perLead)
                this.perLeadStatus = true
                this.monthlyStatus = false
              }
              if (this.countySubCategory[k].subscriptionType === 'monthly') {
                //console.log(this.countySubCategory[k])
                this.totalCostSubscription = this.totalCostSubscription + parseInt(this.countySubCategory[k].monthly)
                this.perLeadStatus = false
                this.monthlyStatus = true
              }
              if (this.counties[i].county === this.countySubCategory[k].county && this.counties[i].stateCode == this.countySubCategory[k].stateCode) {
                this.counties[i].subcategories.push(this.countySubCategory[k])
              }
            }
            //console.log(this.totalCostSubscription)
            localStorage.setItem('serviceCost', this.totalCostSubscription);
            if (i == this.counties.length - 1) {
              this.showConfirmation = true
              this.spinner.hide()
            }
          }
          this.countyList = this.counties
          //console.log(this.counties[i])
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //GET COUNTIES SUB CATEGORY LIST

  //GET ZIPCODES FROM COUNTY
  getZipcodesFromCounty(data, stateCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getZipcodeFromCountyTable/' + data.county + '/' + stateCode + '/' + this.activatedRoute.snapshot.params['prozId'], options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.displayZipcodes = response.response;
          this.displayZipcodes.forEach((e) => {
            e.proCode = this.activatedRoute.snapshot.params['prozId']
            e.checked = true
          })
          return false;
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  //GET ZIPCODES FROM COUNTY

}
