import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProzProfileLayoutComponent } from './proz-profile-layout/proz-profile-layout.component';
import { ProzProfileComponent } from './proz-profile/proz-profile.component';
import { ProzProfileConfirmationComponent } from './proz-profile-confirmation/proz-profile-confirmation.component';
import { ProzProfilePaymentComponent } from './proz-profile-payment/proz-profile-payment.component';
import { ProzProfileServiceAreasComponent } from './proz-profile-service-areas/proz-profile-service-areas.component';
import { MyProzProfileComponent } from './my-proz-profile/my-proz-profile.component';
import { ProzPublicProfileComponent } from './proz-public-profile/proz-public-profile.component';
import { ProEditMyProfileComponent } from './pro-edit-my-profile/pro-edit-my-profile.component';
import { ProzSubscriptionConfirmationComponent } from './proz-subscription-confirmation/proz-subscription-confirmation.component';
const routes: Routes = [
  {
    path: '', component: ProzProfileLayoutComponent,

    children: [
      {
        path: 'prozProfile/:prozId',
        component: ProzProfileComponent
      },
      {
        path: 'prozProfileConfirmation/:prozId',
        component: ProzProfileConfirmationComponent
      },
      {
        path: 'prozProfilePayment/:prozId',
        component: ProzProfilePaymentComponent
      },
      {
        path: 'prozProfileServiceAreas/:prozId',
        component: ProzProfileServiceAreasComponent
      },
      {
        path: 'proEditMyProfile/:prozId',
        component: ProEditMyProfileComponent
      },
      {
        path: 'proPublicProfile',
        component: ProzPublicProfileComponent
      },
      {
        path: 'myProzProfile/:prozId',
        component: MyProzProfileComponent
      },
      {
        path: 'prozSubscriptionConfirmation/:prozId',
        component: ProzSubscriptionConfirmationComponent
      },
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProzProfileRoutingModule { }
